.popup {
  position: absolute;
  border:#61dafb 1px solid;
  padding: 2em;
  background-color: #124861b0;
  visibility: hidden;
  -webkit-animation: one 1000ms 1 6000ms ease-in-out;
          animation: one 1000ms 1 6000ms ease-in-out;
  text-transform: uppercase;
  -webkit-animation: two 2000ms 1 7000ms steps(2, end);
          animation: two 2000ms 1 7000ms steps(2, end);
}
.popup p {
  font-size:10px;
}
.popup span{
  -webkit-animation:loading2 300ms infinite steps(1, end);
          animation:loading2 300ms infinite steps(1, end);
}

@-webkit-keyframes one {
  0%{
    visibility: visible;
    -webkit-transform: scale(0%);
