    -webkit-transform: scaleY(50%);
            transform: scaleY(50%);
  }
  35% {
    -webkit-transform: scaleY(40%);
            transform: scaleY(40%);
    -webkit-transform: scaleX(100%);
            transform: scaleX(100%);
  }
  70% {
    -webkit-transform: scaleY(100%);
            transform: scaleY(100%);
  }
}

